import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Card, Row, Col, Button, message, Popconfirm, Badge, Table } from 'antd';
import MainModal from '../../../../common/components/MainModal'
import { postAction } from '../../../../axios';
import './style.less'

class EarlyWarningConfig extends Component {
    constructor(props){
        super(props);
        
        this.state = ({            
            pageNum: 1,
            pageSize: 10,
            total:0,
            dataLists:[],
            isEdit: false,
            editTag: {},
            showDetail:false,
            detailInfo:{}
        })
    }
    componentWillMount(){
        this.getData();
    }
    getData = () => {
        let data={pageInfo: {pageNum:this.state.pageNum,pageSize:this.state.pageSize}}
        postAction("/bigdata/alarmStrategy/getPageData", data).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        dataLists: res.obj.list || [],
                        total: res.obj.total
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    deleteAction = (id)=> {
        let data = { id }
        postAction("/bigdata/alarmStrategy/delById", data).then(
            (res) => {
                if (res.success) {
                    message.success("删除成功！");
                    this.getData();
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    changePage = (pagination)=> {
        this.setState({pageNum:pagination.current,pageSize:pagination.pageSize},()=>{
           this.getData();
        })
    }
    rowClick = (record)=> {
        let data = {id:record.id}
        postAction("/bigdata/alarmStrategy/findById", data).then(
            (res) => {
                if (res.success) {
                    this.setState({
                        showDetail: true,
                        detailInfo:Object.assign({},res.obj,record)
                    })
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    render() {
        let columns = [{
            title: '序号',
            dataIndex: 'index',
            width: '6%',
        }, {
            title: '预警策略名称',
            dataIndex: 'strategyName',
            width: '18%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '预警策略对象',
            dataIndex: 'strategyTargetCollegeName',
            width: '18%',
            render: (text) => {
            	return (
            		<span title={text}>{text}</span>
            	)
            }
        }, {
            title: '预警策略时间',
            dataIndex: 'dateTime',
            width: '18%',
            render:(text,record)=>{
                return record.startDate + '~' + record.endDate;
            }
        }, {
            title: '预警推送方式',
            dataIndex: 'pushType',
            width: '15%',
        }, {
            title: '状态',
            dataIndex: 'status',
            width: '10%',
            render: (text, record)=>{
                let badge;
                if(text==='生效'){
                    badge = <Badge status="processing" />
                } else if(text==='未生效'){
                    badge = <Badge status="success" />
                } else {
                    badge = <Badge status="default" />
                }
                return <span>{badge}{text}</span>;
            }
        }, {
            title: '操作',
            dataIndex: 'handle',
            render: (text, record)=>(
                <div>
                    <a style={{color:'#309cff',marginRight:10}} onClick={()=>{
                        this.props.history.push('/mainframe/systemsetup/earlywarningconfigadd?id='+record.id)
                    }} href={'javascript:void(0)'}>编辑</a>
                    <Popconfirm title="确认删除本预警策略及对应白名单配置?" onConfirm={() => this.deleteAction(record.id)}>
                        <a href="javascript:;" style={{color:'#8d99b0'}}>删除</a>
                    </Popconfirm>
                </div>
            ),
            onCell:()=>{
                return {
                    onClick:(e)=>e.stopPropagation()
                }
            }
        }];
        const dataSource = this.state.dataLists || [];
        dataSource.forEach((item,index)=>{
            dataSource[index].key = index;
            dataSource[index].index = index + 1;
        })
        
        return (
            <Card className={this.props.className+' earlyWarningConfig'}>
                <Row>
                    <Col span={12}>
                        <div className="common-title">当前预警配置</div>
                    </Col>
                    <Col span={12} style={{textAlign:'right'}}>
                        <Button icon="plus" type={'primary'} onClick={()=>this.props.history.push('/mainframe/systemsetup/earlywarningconfigadd')}>新增策略</Button>
                    </Col>
                </Row>
                <Row style={{marginTop:14}}>
                    <Table dataSource={dataSource} 
                        rowClassName="clickRow"
                        columns={columns}
                        size="middle"
                        pagination={{
                            size: "small",
                            current:this.state.pageNum,
                            total:this.state.total,
                            showQuickJumper: true,
                            showTotal: total => `共 ${total} 条数据`
                        }}
                        bordered
                        onChange={this.changePage.bind(this)}
                        onRow={(record, index) => {
                            return {
                              onClick: () => this.rowClick(record, index),// 点击行
                            };
                        }} />
                </Row>
                {this.state.showDetail?(
                    <MainModal
                        title={'查看详情'}
                        visible={this.state.showDetail}
                        onCancel={()=>this.setState({showDetail:false})}
                        width={656}
                        noFooter={true}
                    >
                        <div className="ewc-detailModel">
                            <Row><Col span={8}>策略名称</Col><Col span={16}>{this.state.detailInfo.strategyName}</Col></Row>
                            <Row style={{height:102}}>
                                <Col span={8}><Row type="flex" align="middle" justify="center">策略对象</Row></Col>
                                <Col span={16}>
                                    <Row type="flex" align="middle" style={{overflowY:'auto'}}>
                                        {this.state.detailInfo.strategyTargetCollegeName}
                                    </Row>
                                </Col>
                            </Row>
                            <Row><Col span={8}>策略时间</Col><Col span={16}>{this.state.detailInfo.startDate + '～' + this.state.detailInfo.endDate}</Col></Row>
                            <Row><Col span={8}>失联预警</Col><Col span={16}>红：{this.state.detailInfo.alarmMissPara.redVal}天、橙：{this.state.detailInfo.alarmMissPara.orangeVal}天、黄：{this.state.detailInfo.alarmMissPara.yellowVal}天、蓝：{this.state.detailInfo.alarmMissPara.blueVal}天</Col></Row>
                            <Row>
                                <Col span={8}>网络预警</Col>
                                <Col span={16}>
                                红：{this.state.detailInfo.alarmNetworkPara.redVal}小时、橙：{this.state.detailInfo.alarmNetworkPara.orangeVal}小时、黄：{this.state.detailInfo.alarmNetworkPara.yellowVal}小时、蓝：{this.state.detailInfo.alarmNetworkPara.blueVal}小时
                                </Col>
                            </Row>
                            <Row><Col span={8}>挂科预警</Col><Col span={16}>红：{this.state.detailInfo.alarmFailPara.redVal}、橙：{this.state.detailInfo.alarmFailPara.orangeVal}、黄：{this.state.detailInfo.alarmFailPara.yellowVal}、蓝：{this.state.detailInfo.alarmFailPara.blueVal}</Col></Row>
                            <Row><Col span={8}>逃课预警</Col><Col span={16}>红：{this.state.detailInfo.alarmCoursePara.redVal}%、橙：{this.state.detailInfo.alarmCoursePara.orangeVal}%、黄：{this.state.detailInfo.alarmCoursePara.yellowVal}%、蓝：{this.state.detailInfo.alarmCoursePara.blueVal}%</Col></Row>
                            <Row><Col span={8} style={{height:83,lineHeight:'42px'}}>消费预警</Col><Col span={16} style={{height:83}}><p style={{marginBottom: 5}}>红：{this.state.detailInfo.alarmConsumePara.redMinVal}-{this.state.detailInfo.alarmConsumePara.redMaxVal}元、橙：{this.state.detailInfo.alarmConsumePara.orangeMinVal}-{this.state.detailInfo.alarmConsumePara.orangeMaxVal}元、</p><p style={{marginBottom: 0}}>黄：{this.state.detailInfo.alarmConsumePara.yellowMinVal}-{this.state.detailInfo.alarmConsumePara.yellowMaxVal}元、蓝：{this.state.detailInfo.alarmConsumePara.blueMinVal}-{this.state.detailInfo.alarmConsumePara.blueMaxVal}元</p></Col></Row>
                            <Row><Col span={8} style={{height:88,lineHeight:'42px'}}>夜归预警</Col><Col span={16}><p style={{marginBottom: 5}}>夜归时间{this.state.detailInfo.alarmBackPara.nightBackTime}-{this.state.detailInfo.alarmBackPara.notBackTime} </p><p style={{marginBottom: 0}}>红：{this.state.detailInfo.alarmBackPara.redVal}次、橙：{this.state.detailInfo.alarmBackPara.orangeVal}次、黄：{this.state.detailInfo.alarmBackPara.yellowVal}次、蓝：{this.state.detailInfo.alarmBackPara.blueVal}次</p></Col></Row>
                        </div>
                    </MainModal>
                ):null}
            </Card>
        )
    }
}

export default withRouter(EarlyWarningConfig);


